:root{
    --heights: 100vh;
    --widths: 100%;
}

.slider-container{
    height: var(--heights);
    width: var(--widths);
    position: relative;
    margin: auto;
    overflow: hidden;
}

.active{
    display: inline-block;
}

.inactive{
    display: none;
}

.slides{
    height: var(--heights);
    width: var(--widths);
    position: relative;
}

.slide-image{
    width: var(--widths);
    height: 100%;
    position: relative;
    object-fit: cover;
}

.slide-title, .slide-desc{
    width: var(--widths);
    height: 100%;
    color: #fff;
    font-size: 2rem;
    position: absolute;
    text-align: center;
    top: 40%;
    z-index: 10;
}

.slide-desc{
    top: 55%;
    font-size: 2rem;
}

.prev, .next{
    cursor: pointer;
    z-index: 100;
    position: absolute;
    top: 50%;
    width: auto;
    padding: 1rem;
    margin-top: -3rem;
    font-size: 40px;
    font-weight: bold;
    border-radius: 0px 5px 5px 0px;
    background: #fff;
    opacity: 0.3;
}

.prev:hover, .next:hover{
    color: #fff;
    background-color: rgba(0, 0, 0, 0.6);
    opacity: 1;
    transition: all 0.5s ease-in;
}

.next{
    right: 0;
    border-radius: 5px 0px 0px 5px;
}

.all-dots{
    width: var(--widths);
    height: 100%;
    position: absolute;
    display: flex;
    top: 85%;
    justify-content: center;
    z-index: 200;
}

.dot{
    cursor: pointer;
    height: 1rem;
    width: 1rem;
    margin: 0px 3px;
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 50%;
    display: inline-block;
}

.active-dot,
.dot:hover{
    background-color: rgba(255, 255, 255, 0.5);
}